<template>
    <aside class="side">
        <a href="./">
            <img class="logo" src="@/assets/image/logo.svg" alt="Spott">
        </a>
        <nav class="menu">
            <router-link to="/" class="menu__list">TOP</router-link>
            <router-link to="/system" class="menu__list">デザインシステム</router-link>
            <router-link to="/ui" class="menu__list">UI</router-link>
            <router-link to="/web" class="menu__list">Web</router-link>
            <router-link to="/icon" class="menu__list">アイコン</router-link>
            <router-link to="/illust" class="menu__list">イラスト</router-link>
            <router-link to="/color" class="menu__list">配色</router-link>
            <router-link to="/animation" class="menu__list">アニメーション</router-link>
            <router-link to="/podcast" class="menu__list">Podcast</router-link>

            <a class="menu__list" href="https://www.notion.so/akinen/Spott-79dc82356a2a4a15b52a696a9fbf8315">その他</a>
        </nav>
        <a class="feedback" href="https://twitter.com/_akinen" target="__blank">
            <img class="feedback__icon" src="@/assets/image/akinen.png" alt="akinen">
            <p class="feedback__title">Follow Me</p>
            <p class="feedback__description">改善のアイデアも募集しています</p>
        </a>
    </aside>
</template>

<script>
export default {
  name: 'GlobalMenu'
}
</script>

<style lang="stylus" scoped>
.side
  position fixed
  top 0
  left 0
  width 360px
  height 100vh
  background-color #111424

.logo
  width 160px
  margin 32px 0 0 105px

.menu
  display flex
  flex-direction column
  margin-top 48px
  width 255px
  float right

  &__list
    position relative
    color #fff
    font-size 20px
    font-weight bold
    padding 13px 0 13px 32px
    text-decoration none

    &:hover:not(.router-link-exact-active)
      border-radius 100px
      background-color rgba(255, 255, 255, 0.2)
      transition background-color 0.25s

  .router-link-exact-active
    color #222
    background-color #fff
    border-radius 28px 0 0 28px

    &::before,
    &::after
      content ''
      position absolute
      display block
      width 28px
      height 28px
      right 0
      background-image url('https://akinening.github.io/bookmark/assets/rounded.svg')
      background-size cover

    &::before
      top -28px

    &::after
      bottom -28px
      transform scale(1, -1)

.feedback
  position absolute
  bottom 32px
  right 32px
  width 243px
  height 64px
  padding 12px 12px 12px 62px
  box-sizing border-box
  border-radius 100px
  background-color rgba(255, 255, 255, 0.2)

  &__icon
    position absolute
    top 12px
    left 12px
    width 40px
    height 40px
    border-radius 20px

  &__title
    margin-bottom 8px
    color white
    font-size 16px
    font-weight bold

  &__description
    color #ccc
    font-size 11px
</style>



